
import React,{Component} from 'react';
import background1 from "../photos/1.png";
import background2 from "../photos/2.png";
import background3 from "../photos/3.png";
import background4 from "../photos/4.png";

class project extends Component {
    constructor(props) {
        super(props)
    
        this.state = 
        {
            items:
            [
                {
                 id:1,
                 title:'WHO AM I? üòÉ',
                 img:background1,
                 body:'Simple portfolio build using ReactJs‚öõÔ∏è , HTML , CSS3 ,Bootstrap4 ',
                 glink:'',
                 wlink:''
                },
                {
                    id:2,
                    title:'Checklist ‚úîÔ∏è',
                    img:background2,
                    body:'Simple Checklist build using ReactJs‚öõÔ∏è , HTML , CSS3 ,Bootstrap4 ',
                    glink:'',
                    wlink:''
                   },
                   {
                    id:3,
                    title:'Remainder ‚è∞',
                    img:background3,
                    body:'Simple Remainder build using NodeJs , Express , JavaScript , mongoDB , HTML , CSS3 ,Bootstrap4 ',
                    glink:'',
                    wlink:''
                   },
                   {
                    id:4,
                    title:'Merchandise üõí',
                    img:background4,
                    body:'E-commerce website(Front-End) build using  HTML , CSS3 , JavaScript ',
                    glink:'',
                    wlink:''
                   },
                   
                          
            ]

             
        }
    }
    
    render()
    {
        let itemList=<div>EMPTY</div>
        if(this.state.items.length)
        {
            itemList=this.state.items.map(item =>
            {
              let Background=item.img;
              let styles =
             {
                backgroundImage: `url(${Background})`
    
             }
             let delay=1000+item.id*200;
             return(
                <div className="col-lg-3 col-sm-5 col-xs-8 p-item" key={item.id} data-aos="fade-up" data-aos-once="true" data-aos-duration="1800" data-aos-delay={delay}>
                <p className="p-title">{item.title} </p>
                <div className="p-img" style={styles}></div>
                <p>{item.body}</p>
                <div className="link">
                    <a href={item.wlink}><i class="fas fa-link wlink"></i></a>
                    <a href={item.glink}><i class="fab fa-github-alt glink"></i></a>
                </div>
            </div>    
             )

            })
        }
        
        return (
            <div className="p-cont" id="project">
            <p className="topic" data-aos="fade-up" data-aos-once="true" data-aos-duration="800" data-aos-delay="1000">Projects:</p>
              <div className="container-fluid">
                  <div className="row row-p ">
                        {itemList} 
                  </div>
              </div>
            </div>
        )

    }
    
}

export default project
