import React,{Component} from 'react'
import AOS from 'aos';
import 'aos/dist/aos.css'; // You can also use <link> for styles
// ..
AOS.init();
AOS.init({
    // Global settings:
    disable: false, // accepts following values: 'phone', 'tablet', 'mobile', boolean, expression or function
    startEvent: 'DOMContentLoaded', // name of the event dispatched on the document, that AOS should initialize on
    initClassName: 'aos-init', // class applied after initialization
    animatedClassName: 'aos-animate', // class applied on animation
    useClassNames: false, // if true, will add content of `data-aos` as classes on scroll
    disableMutationObserver: false, // disables automatic mutations' detections (advanced)
    debounceDelay: 50, // the delay on debounce used while resizing window (advanced)
    throttleDelay: 99, // the delay on throttle used while scrolling the page (advanced)
    
  
    // Settings that can be overridden on per-element basis, by `data-aos-*` attributes:
    offset: 120, // offset (in px) from the original trigger point
    delay: 200, // values from 0 to 3000, with step 50ms
    duration: 1000, // values from 0 to 3000, with step 50ms
    easing: 'ease', // default easing for AOS animations
    once: true, // whether animation should happen only once - while scrolling down
    mirror: false, // whether elements should animate out while scrolling past them
    anchorPlacement: 'top-bottom', // defines which position of the element regarding to window should trigger the animation
  
  });
class hobby extends Component {
   constructor(props) {
       super(props)
   
       this.state = 
       {
           items:[
           { id:1,
             name:"ReactJS",
             src:"https://img.icons8.com/plasticine/100/000000/react.png",
             width:43,
             alt:"ReactJS img"
           },
           { id:2,
            name:"HTML",
            src:"https://img.icons8.com/dusk/100/000000/html-5.png",
            width:43,
            alt:"HTML img"
          },
          { id:3,
            name:"CSS3",
            src:"https://img.icons8.com/dusk/64/000000/css3.png",
            width:43,
            alt:"CSS3 img"
          },
          { id:4,
            name:"JavaScript",
            src:"https://img.icons8.com/dusk/64/000000/javascript.png",
            width:43,
            alt:"JavaScript img"
          },
          { id:5,
            name:"NodeJS",
            src:"https://img.icons8.com/windows/64/000000/node-js.png",
            width:43,
            alt:"NodeJs img"
          },
          { id:6,
            name:"mongoDB",
            src:"https://img.icons8.com/color/96/000000/mongodb.png",
            width:43,
            alt:"mongoDB img"
          },
          { id:7,
            name:"C++",
            src:"https://img.icons8.com/color/96/000000/c-plus-plus-logo.png",
            width:43,
            alt:"C++ img"
          },
          { id:8,
            name:"C",
            src:"https://img.icons8.com/color/96/000000/c-programming.png",
            width:43,
            alt:"C img"
          },
          { id:9,
            name:"Bootstrap4",
            src:"https://img.icons8.com/color/64/000000/bootstrap.png",
            width:43,
            alt:"Bootstrap4 img"
          },
          { id:10,
            name:"Python",
            src:"https://img.icons8.com/dusk/100/000000/python.png",
            width:43,
            alt:"python img"
          }
        ]

       }
   }
   
   render(){
       let itemList=<div>EMPTY</div>
       if(this.state.items.length)
       {
           itemList=this.state.items.map(item=>{
              return(
                <div className="col-sm-2 col-xs-1 col-item" key={item.id}>
                <img src={item.src} width={item.width} alt={item.alt} />
                <p>{item.name}</p>
                </div>
              )
           })
       }
        return ( <div className="skill-class" data-aos-once="true">
                    <p className="topic" data-aos="fade-up" data-aos-once="true" data-aos-duration="1600" data-aos-delay="800">Skillset:</p>
                      <div className="cont-o">
                        <div className="container-fluid">
                          <div className="row row-1" data-aos="fade-up" data-aos-once="true" data-aos-duration="1600" data-aos-delay="1000">
                             {itemList}
                          </div>
                        </div>
                    </div>
                  </div>
                )
        }
}

export default hobby



