import React from 'react'

function joke4() {
    return (
        <div className="joke" data-aos="fade-up" data-aos-once="true" data-aos-duration="800" >
            <blockquote >
            "It Doesn't work.....why?
            <br></br>
            After Random Change , it Works...why?  "
            </blockquote>
        </div>
    )
}
 
export default joke4
